.sidebar-container {
  //width: 18%;
  //max-width: 300px;
  /*background-color: #263141;*/
  display: flex;
  padding-right: 10px;
  height: 100%;
}

.sidebar-hidden .sidebar-main {
  display: none;
}

.sidebar-main {
  display: flex;
  flex-direction: column;
  //border-right: 1px solid #ddd;
}

.sidebar-expand-disable .expand-left {
  display: none;
}

.expand-left {
  display: flex;
  width: 14px;
  height: 100%;
  align-items:center;
}

.sidebar-hidden .expand-left-btn:hover {
  //background-position: -28px -11px;
  background-position: -29px -11px;
}

.sidebar-hidden .expand-left-btn {
  //background-position: -42px -11px;
  background-position: -43px -11px;
}

.expand-left-btn:hover{
  //background-position:-0px -11px;
  background-position:-1px -11px;
}

.expand-left-btn {
  width: 13px;
  height: 5pc;
  background: url(./img/expand-btn.png) no-repeat;
  //background-position: -14px -11px;
  background-position: -15px -11px;
}

/*search-bar样式*/
.search-bar {
  height: 24px;
  line-height: 24px;
  text-align: center;
}
.search-bar .ant-input-search-wrapper {
  width: 80%;
}
.search-bar .ant-input {
  height: 24px;
  /*border-radius: 17px;*/
  /*border: 1px solid #1d2634;*/
  /*font-size: 16px;*/
  /*color: white;*/
  /*background-color: #1d2634;*/
  padding-left: 16px;
}

