.search-bar .ant-input:hover {
  border: 1px solid #1d2634;
}
.search-bar .ant-input:focus {
  border: 1px solid #1d2634;
  box-shadow:0 0 0;
}
.search-bar .ant-input-search-wrapper .ant-input-search-icon {
  font-size: 20px;
  color: #386bb3;
}

/*搜索结果*/
.ant-tree-searchable-filter {
  color: #f50;
  transition: all .3s ease;
}
.sidebar-container .ant-spin-nested-loading {
  overflow: auto;
  flex-grow: 1;
  display: flex;
}
.user-tree {
  overflow: auto;
  /*padding-left: 10%;*/
  padding-bottom: 4px;
  flex-grow: 1;
  display: flex;
  .rc-tree-node-selected {
    .title-text {
      background-color: #e4e6e6;
      color: #383838;
      border-radius: 2px;
      padding: 0 5px 2px;
      -moz-user-select: none;
      -webkit-user-select: none;
      -ms-user-select: none;
      -o-user-select: none;
      user-select: none;
    }
  }
  .rc-tree {
      padding: 0px;
  }
}
.sidebar-container .ant-spin-container {
  display: flex;
  flex-grow: 1;
}


