#treeDemo {
  height: 100%;
}

.sidebar-container .select-menu {
  position: fixed;
  z-index: 100;
  background: white;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.2);
  font-size: 13px;

  li {
    border-bottom: 1px solid #e9e9e9;
    margin: 0 15px;
    padding: 10px 0;
    cursor: pointer;
    transition: color 0.5s ease;
  }
  li:hover {
    color: #3e7cd1;
  }

  li:last-child{
    border-bottom: 0;
  }
  li:first-child {
    cursor: default;
    color: rgb(236,131,80);
    font-size: 16px;
  }
}

/*ant-tree*/
.user-tree span.rc-tree-title {
    /*color: white;*/
}

.xiaomi-icon {
  background: url(./img/xiaomi.png) no-repeat;
  display: inline-block;
  height: 17px;
  width: 17px;
}

.tree-title {
  display: inline-block;
  position: relative;
  /*padding-left: 20px;*/
}
.title-icon,.title-icon-xiaomi,.title-icon-house {
  display: inline-block;
  position: absolute;
  top: 3px;
  height: 17px;
  width: 17px;
}
.title-icon {
  background: url(./img/file.png) no-repeat;
  /*background-color: red;*/
}
.title-icon-xiaomi {
  background: url(./img/xiaomi.png) no-repeat;
  & + .title-text {
    padding: 0 !important;
  }
}
.title-icon-house {
  background: url(./img/house.png) no-repeat;
}

.title-text {
  margin-left: 20px;
  /*color: white;*/
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
}
.ant-tree-searchable-filter {
  color: red;
}

.rc-tree-node-selected {
  background-color: transparent;
  border: 0px #ffb951 solid;
  opacity: 1;
}

html.runmode-xiaomi{
  .non-xiaomi{
    display: none;
  }
  .only-xiaomi{
    display: initial;
  }
  .title-icon-cluster {
    background: url(./img/cluster.png) no-repeat;
  }

  .title-icon-cop {
    background: url(./img/cop.png) no-repeat;
  }

  .title-icon-job {
    background: url(./img/job.png) no-repeat;
  }

  .title-icon-jobgroup {
    background: url(./img/jobgroup.png) no-repeat;
  }

  .title-icon-owt {
    background: url(./img/owt.png) no-repeat;
  }

  .title-icon-pdl {
    background: url(./img/pdl.png) no-repeat;
  }

  .title-icon-service {
    background: url(./img/service.png) no-repeat;
  }

  .title-icon-servicegroup {
    background: url(./img/servicegroup.png) no-repeat;
  }
  
  .title-icon-sbs {
    background: url(./img/sbs.png) no-repeat;
  }
  
  .title-icon-mod {
    background: url(./img/mod.png) no-repeat;
  }
  
  .title-icon-srv {
    background: url(./img/srv.png) no-repeat;
  }
  
  .title-icon-grp {
    background: url(./img/grp.png) no-repeat;
  }
  
  .title-icon-status {
    background: url(./img/status.png) no-repeat;
  }
  
  .rc-tree-node-content-wrapper-open > .rc-tree-title > .tree-title{
    > .title-icon-owt {
      background-image: url(./img/owt-open.png);
    }
    > .title-icon-service {
      background-image: url(./img/service-open.png);
    }
  }
}

html:not(.runmode-xiaomi){
  .non-xiaomi{
    display: initial;
  }
  .only-xiaomi{
    display: none;
  }
}

.seek {
  height: 40px;
  border-radius: 10px;
  outline: none;
  background: #f7f7f7;
  border: 1px solid #d9d9d9;
  padding-left: 10px;
  margin-right: 10px;
  background-color: #fff;
  width: 80%;
}
.seek:hover {
  border: 1px solid #2db7f5;
  background: #fff;
}
.addnode {
  padding: 10%;
}
.addnode_button {
  margin-top: 20px;
  float: right;
}
.modal_tag {
  width: 720px !important
}
.seeks {
  width: 80%;
  height: 30px
}
.sift {
  margin: 20px 0;
}

.sidebar-none {
  display: none;
}



