.canvas{
  background: #FFF;
  margin: 20px;
}
.header{
  height: 40px;
  line-height: 40px;
  background: #3e7cd1;
  padding-left: 10px;
}
// charts style
.charts-container{
  box-sizing: border-box;
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  align-content: flex-start;
  padding: 0 20px 10px 20px;
}
.no-data {
  box-sizing: border-box;
  flex: 0 0 calc((100% - 10px) / 2);
  padding: 0 5px;
  border: 1px solid #eee;
  overflow: hidden;
  height: 400px;
}
.charts {
  box-sizing: border-box;
  flex: 0 0 calc((100% - 10px) / 2);
  padding: 0 5px;
  border: 1px solid #eee;
  overflow: hidden;
}
.charts:nth-child(odd) {
  margin: 5px 10px 5px 0;
}
.charts:nth-child(even) {
  margin: 5px 0;
}
// charts title styles
.charts .highcharts-title {
  width: 90%;
}
.dashboard-chart-title {
  word-wrap: break-word;
  white-space: normal;
  text-align: center;
  font-size: 12px;
}
// charts form styles
#charts-form {
  margin: 30px 10px 25px 20px;
}
#charts-form .ant-select {
  top: 0;
}
a.back{
  color: #FFF;
  font-size: 25px;
  float: right;
  margin-right: 20px;
}
// charts legend styles
.dashboard-legend-container {
  min-height: 18px;
  box-sizing: border-box;
  font-weight: normal;
  font-family: 'verdana', 'Microsoft YaHei', 'Consolas', 'Deja Vu Sans Mono', 'Bitstream Vera Sans Mono';
  display: flex;
  align-items: flex-start;
}
.dashboard-legend-name {
  box-sizing: border-box;
  font-weight: normal;
  font-family: 'verdana', 'Microsoft YaHei', 'Consolas', 'Deja Vu Sans Mono', 'Bitstream Vera Sans Mono';
  word-break: break-all;
  white-space: normal;
}
.dashboard-legend-num {
  box-sizing: border-box;
  overflow: visible;
}
.dashboard-legend-num span {
  margin-left: 5px;
  display: inline-block;
  word-wrap: break-word;
  white-space: normal;
}
// tooltip styles
.highcharts-tooltip .dashboard-tooltip{
  background-color: #FFF;
  opacity: 1;
  z-index: 9999!important;
  padding: 5px;
  border: 1px solid #eee;
}
// loading state
.dashboard-showcharts-loading {
  position: absolute;
  z-index: 99;
  top: 50%;
  left: 50%;
}



