// dashboard layout
#dashboard-container {
  display:flex;
  align-items: flex-start;
}
.dashboard-searchdata-loading {
  position: absolute;
  z-index: 99;
  top: 50%;
  left: 30%;
}
#dashboard-container .endpoints{
  width:25%;
  margin:20px 0 20px 20px;
  display:flex;
  flex-direction: column;
}
.up{
  background: #FFF;
  padding:10px 0;
}
#dashboard-container .counters{
  width:75%;
  margin:20px;
  display:flex;
  flex-direction: column;
}
.header{
  font-size: 16px;
  line-height: 40px;
  color:#FFF;
  background: #3e7cd1;
}
.header .num{
  display: inline-block;
  background: #000;
  width:40px;
  height: 40px;
  text-align: center;
  margin-left: -10px;
}
.title{
  display: inline-block;
  height: 40px;
  padding-left: 20px;
}
.content{
  display: flex;
  flex-direction: column;
}
#dashboard-container .ant-input{
  margin-top:5px;
  margin-bottom:5px;
}
#dashboard-container .ant-form-item {
  margin-bottom: 0px;
}
.down{
  background: #FFF;
  margin-top: 20px;
  text-align: center;
  height:100px;
  line-height: 100px;
}
#dashboard-container .ant-form-item-label{
  line-height: 48px;
}

// endpoints form styles
#table-header{
  margin-top:20px;
  display: flex;
  height:50px;
  background: #FFF;
  padding:10px;
}
#table-header .ant-input {
  height: 33px;
}
#table-header .ant-input-affix-wrapper {
  bottom: 3px;
}
// reset Input style
#table-header .ant-input{
  margin:0;
}
// reset Select control style
#table-header .ant-select {
  margin-right: 20px;
}
#table-header .check{
  margin-left: auto;
}
// endpoints result styles
#endpoints-results tr:nth-child(odd) {
  background: #FFF;
}
#endpoints-results tr:nth-child(even) {
  background: #f7f7f7;
}
// counter result styles
#counters-results .title{
  margin-top:20px;
  display: flex;
  height:50px;
  background: #FFF;
  padding:10px;
}
#counters-results .ant-input {
  height: 32px;
}
#counters-results .ant-input-affix-wrapper {
  bottom: 2px;
}
// reset Input style
#counters-results .ant-input{
  margin:0;
}
// reset Select control style
#counters-results .ant-select {
  margin-right: 20px;
}
#counters-results .check{
  margin-left: auto;
  font-size:16px;
}
#counters-results tr:nth-child(odd) {
  background: #FFF;
}
#counters-results tr:nth-child(even) {
  background: #f7f7f7;
}
.copy-counters .ant-modal-content{
  padding-bottom: 20px;
}
.copy-counters .ant-modal-footer{
  display: none;
}
.copy-counters .ant-input{
  margin-bottom: 10px;
}
.copy-counters .tip{
  float:right;
}
.copy-counters .tip:after{
  display:block; content:''; clear:both; height:0; visibility:hidden;
}



