.tab-page{
  padding: 19px 16px;
  .bread-container {
    font-size: 14px;
    min-height: 21px;
    .ant-breadcrumb{
      color: #333;
      .ant-breadcrumb-separator{
        color: inherit !important;
      }
      & > span:last-child{
        font-weight: initial !important;
        color: inherit !important;
      }
    }
  }
  .header-ctrls {
    height: 66px;
    line-height: 36px;
    padding: 15px;
    background: #fcfcfc;
  }
  .body-content {

  }
  .c-red {
    color: #ff0000;
  }
  .padded {
    padding: 20px;
  }
  .bmr {
    margin-right: 30px;
  }
  .anno {
    color: rgb(172, 172, 172);
  }
  .line {
    padding: 10px 0;
  }
  .search {
    width: 200px;
    line-height: 28px;
    vertical-align: middle;
  }
  .show-container {
    background-color: white;
    margin-top: 20px;
    min-height: 66vh;
  }
  &:after {
    content: '';
    clear: both;
  }
}

table{
  .note {
    color: rgb(193, 193, 193);
  }
  tr{
    th{
      background-color: #fcfcfc !important;
      font-size: 14px !important;
      color: #383838 !important;
    }
    & > td:not(.center){ 
    /*
      &:first-child{
        padding-left: 14px;
      }
    */
    }
  }
}

.contact-card {
  width: 400px;
  height: 200px;
  position: relative;
  ul {
    width: 200px;
    li {
      height: 40px;
      line-height: 40px;
    }
  }
  .qr {
    position: absolute;
    top: 0;
    right: 0;
    width: 180px;
    height: 100%;
    text-align: center;
    canvas {
      margin-top: 5px;
    }
    &:active {
      background: rgb(63, 117, 187);
      p {
        color: #fff;
      }
    }
    p {
      position: absolute;
      right: 0;
      width: 100%;
      font-size: 14px;
      line-height: 14px;
      &:nth-of-type(1) {
        bottom: 25px;
      }
      &:nth-of-type(2) {
        bottom: 5px;
      }
    }
  }
}

.fl {
  float: left;
}
.fr {
  float: right;
}
.tr {
  text-align: right;
}
.mr {
  margin-right: 10px;
}
.ml {
  margin-left: 10px;
}
.rb{
  font-size: 40px;
  width: 40px;
  height: 40px;
}
a.optn:not(:last-child){
  margin-right: 10px;
}

.uic-team{
  margin-right: 10px;
}

html{
  font-family: "Microsoft YaHei" !important;
}

.cb{
  clear: both;
}

.wk-ch{
  color: rgba(0, 0, 0, .5);
}

.st-ch{
  color: rgba(0, 0, 0, 1);
}

.operations > a:not(:last-child), .operations > .item:not(:last-child) {
  margin-right: 10px;
}

.ctl-tbl-topbar {
  display: flex;
  justify-content: space-between;
  padding-bottom: 5px;
}

.tbl-topbar-right {
  display: flex;
  justify-content: flex-end;
}

.ctl-tbl {
  padding-bottom: 5px;
}

.ctl-tbl .ant-btn, .ctl-tbl-topbar .ant-btn {
  margin-right: 5px;
  height: 22px;
}

.st-add-bg, .st-success-bg {
  background: #10a54a;
  color: #fff;
}

.st-del-bg, .st-error-bg {
  background: #a41e22;
  color: #fff;
}

.st-edit-bg, .st-info-bg {
  background: #c5862b;
  color: #fff;
}

.st-add, .st-save, .st-success {
  color: #10a54a;
}

.st-edit, .st-cancel, .st-info {
  color: #c5862b;
}

.st-del, .st-error {
  color: #a41e22;
}

.st-blod {
  font-weight: bold;
}



