.header-rows {
  display: flex;
  flex-direction: column;
  height: 86px;
  width: 100%;
  flex-shrink: 0;
}

.header-row1 {
  display: flex;
  justify-content: space-between;
  font-size: 15px;
  box-sizing: border-box;
  padding: 0 20px;
  height: 40px;
  line-height: 40px;
  background: #333;
  color: #b0b0b0;
}

.container {
  width: 1226px;
  margin-right: auto;
  margin-left: auto;
}

.header-row1 .header-link {
  color: #b0b0b0;
}

.row1-left {
  height: 40px;
  line-height: 40px;
}

.logo {
  background:url(./mi-logo.png) no-repeat 50% 50%;
  transform: translate3d(0, 0, 0);
  opacity: 1;
  display: block;
  width: 40px;
  height: 40px;
  overflow: hidden;
}

.row1-right {
  height: 40px;
  line-height: 40px;
}

.row1-right .ant-btn, .row1-right .ant-input {
  height: 24px;
  padding: 0 10px;
}

.login-form {
  font-size: 13px;
  height: 40px;
  line-height: 40px;
}


.header-row2 {
  display: flex;
  justify-content: space-between;
  font-size: 15px;
  box-sizing: border-box;
  padding: 0 20px;
  height: 24px;
}

.header-row2 .menu-ul {
  font-weight: bold;
  border-bottom: 1px solid #ddd;
  height: 24px;
  line-height: 24px;
}

.header-row2 .menu-ul .menu-link-selected {
  color: #900;
  border-bottom: 3px solid #900;
}

.header-row2 .menu-link-selected a {
  color: #900;
  text-decoration: none;
}

.header-row2 a {
  color: #232323;
  text-decoration: none;
}

.header-row2 .menu-ul .link-container {
  height: 23px;
  line-height: 23px;
}

.header-row2 .menu-ul li {
  display: inline-block;
  font-size: 18px;
  height: 20px;
  line-height: 20px;
}

.menu-ul {
  display: inline-block;
  float: left;
  overflow: hidden;
  margin: 0;
}

.menu-ul li {
  display: inline-block;
}

.header-row3 {
  display: flex;
  justify-content: space-between;
  margin-top: 1px;
  font-size: 15px;
  box-sizing: border-box;
  padding: 0 20px;
  height: 22px;
  line-height: 22px;
}

.header-row3 a{
  color: #0654ac;
  text-decoration: none;
}


.header-row3 .menu-ul .menu-link-selected {
  background: #D8EDF9;
  pointer-events: none;
}

.header-row3 .menu-link:hover {
  text-decoration: underline;
}

.header-row3 .ant-input{
  height: 22px;
}

.ant-input-suffix {
  padding-top: 3px;
}

.header-link {
  display: inline-block;
  margin-right: 30px;
  font-size: 16px;
}

.menu-link {
  padding: 0 20px;
  display: block;
  white-space: nowrap;
  box-sizing: border-box;
}




